<template>
  <div class="register">
    <header class="header">
      <div class="logo">
        <img src="../../assets/logo.png" alt>
      </div>
      <div class="rightBox">
        <!-- 右边的操作 -->
        <el-button type="primary" @click="toLogin">登录</el-button>
        <el-button type="primary" plain @click="toRegister">注册</el-button>
      </div>
    </header>
    <p style="margin:20px;font-size:20px;font-weight:600;">注册新用户</p>
    <div class="content">
      <div class="allTab" v-if="routePath != '/Register/success'">
        <div class="tab tab-1" :class="{'tabCheck':routePath == '/Register/registerInfo'}">注册信息</div>
        <div class="tab tab-2" :class="{'tabCheck':routePath == '/Register/taobao'}">验证淘宝账号</div>
        <div class="tab tab-3">确认提交</div>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// import tipInput from '@/components/TipInput/tipInput'
export default {
  data () {
    return {
      //   routePath:"/Register/registerInfo"
      routePath: "",
    }
  },
  mounted () {
    this.routePath = this.$route.path
  },
  watch: {
    $route (to, from) {
      console.log(to.path);
      this.routePath = this.$route.path
    }
  },
  methods: {
    toLogin () {
      this.$router.push('/login')
    },
    toRegister () {
      this.$router.push('/Register/registerInfo')
    }
  }
}
</script>

<style lang="scss" scoped>
.register {
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: rgb(242, 242, 242);
  overflow: hidden;
  .header {
    width: 100vw;
    height: 60px;
    background: #fff;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      width: 180px;
      height: 100%;
      img {
        display: block;
        width: 140px;
        margin: auto;
        height: 50px;
        margin-top: 5px;
      }
    }
    .rightBox {
      margin-right: 20px;
      display: flex;
      align-items: center;
    }
  }
  .content {
    background-color: white;
    min-height: 60%;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    padding-top: 30px;
    align-items: center;
    .allTab {
      width: 80%;
      height: 50px;
      display: flex;
      .tab {
        flex: 1;
        height: 100%;
        border: 1px solid rgb(199, 199, 199);
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .tabCheck {
        flex: 1;
        height: 100%;
        border: 1px solid rgb(199, 199, 199);
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgb(64, 158, 255);
        color: white;
      }
      .tab-1 {
        flex: 1;
        border-right: none;
        border-bottom-left-radius: 6px;
        border-top-left-radius: 6px;
      }
      .tab-3 {
        flex: 1;
        border-left: none;
        border-bottom-right-radius: 6px;
        border-top-right-radius: 6px;
      }
    }
  }
}
</style>
<style>
.headerWrapper .el-dropdown {
  color: #000;
  font-size: 16px;
}
</style>



